.hero .right_img img {
  position: absolute;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
  border-radius: 10px;
  top: 131px;
  right: 31%;
  width: 479px;
}
@media screen and (max-width: 768px) {
  .hero .right,
  .hero .left {
    width: 100%;
  }

  .hero .f_flex {
    flex-direction: column;
    flex-direction: column-reverse;
  }
  .hero .right_img {
    width: 100%;
    height: 73vh;
  }

  .hero .right_img img {
    position: absolute;
    position: absolute;
    top: 11%;
    right: 17%;
    width: 67%;
  }
}
@media screen and (max-width: 375px) {
  .hero .right_img img {
    position: absolute;
    top: 13%;
    right: 9%;
    width: 83%;
  }
}
@media screen and (max-width: 390px) {
  .hero .right_img img {
    position: absolute;
    top: 13%;
    right: 9%;
    width: 83%;
  }
}

@media screen and (min-width: 1880px) {
  .hero .right_img img {
    position: absolute;
    top: 40px;
    right: 15%;
    width: 479px;
  }
}
